@charset "utf-8";

body,header,footer,main,nav,aside,article,section,div,h1,h2,h3,h4,h5,h6,address,p,form,fieldset,legend,table,thead,tbody,th,td,ol,ul,li,dl,dt,dd,input,button,select,option,textarea,figure,figcaption,caption,blockquote{margin:0; padding:0;}

ol,ul{list-style:none;}
img{display:block; border:0px;}
a{text-decoration:none; color:initial;}
address,em,i{font-style:normal;}
button,fieldset,select,input{border:0px; background:transparent; text-align: left;}
legend,caption,hr{display:none;}
table{border-collapse:collapse; border-spacing:0px;}
